


class Gps {
	constructor(arg) {
		this.lock = false //
	}
	async getLocation(param = {
		type: 'wgs84'
	}) {
		return new Promise(async (callback) => {
			if (this.lock) {
				// console.log('');
				callback(false);
				return false
			}
			this.lock = true //
			uni.getLocation({
				...param,
				success: res => {
					this.lock = false //
					//console.log(res);
					callback(res)
				},
				fail: async (err) => {
					uni.showToast({
						title: '',
						icon: 'none'
					});
					console.error(err)
					callback(false)
					



					
					

					if (err.errMsg == 'getLocation:fail auth deny') {
						uni.showModal({
							content: '',
							confirmText: '',
							complete: (e) => {
								if (e.confirm) {
									uni.openSetting({
										success(res) {
											console.log(res.authSetting)
										}
									});
								}
								this.lock = false //
							}
						});
					}
					if (err.errMsg == 'getLocation:fail:ERROR_NOCELL&WIFI_LOCATIONSWITCHOFF') {
						uni.showModal({
							content: '',
							showCancel: false,
							confirmText:""
						});
					}

				}
			});
		})
	}

































































}
export default Gps
